.header {
    width: 100%;
    min-height: 50px;
    height: fit-content;
    box-shadow: rgba(0,0,0,0.25) 0 25px 50px -12px;
    display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
    gap: 1em;
}

.header a.active {
    color: orange;
    text-decoration: none;
}